<template>
  <div>
    <div class="d-flex justify-content-center mb-2">
      <button class="btn btn-success" @click="collectDCU">Collect DCU</button>
    </div>
    <div class="list-dcu-container">
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">DCU_id</th>
            <th scope="col">Fw_Ver</th>
            <th scope="col">Channel</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(dcu, idx) in listDcu" :key="idx">
            <th scope="row">1</th>
            <td>{{ dcu.dcu_id }}</td>
            <td>{{ dcu.fw_ver }}</td>
            <td>{{ dcu.channel }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "ListDCU",
  data() {
    return {
      dcu_list: [],
    };
  },
  computed: {
    ...mapGetters(["getListDcu"]),
    listDcu() {
      return this.getListDcu;
    },
  },
  created() {},
  methods: {
    collectDCU() {
      this.$socket.emit("collect_dcu", "hello");
    },
  },
};
</script>

<style>
.list-dcu-container {
  height: 100px;
  /* background-color: rgba(255, 255, 255, 0.836); */
}
</style>
